body {
  max-width: 720px;
  margin-right: auto;
  margin-left: auto;  
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #575655;/* font color */  
  display: grid;  
  /* mobile phone are 360X740  */
  /* 4 times is 1440X2960  */
  /* 2 times is 720X1480 - using this, lets use 900(time 1.5 instead of 1.7like in a real phone) height instead */
  grid-template-columns:1fr 40px 600px 40px 1fr; 
  grid-template-rows:10px 10px repeat(9, 100px);
  background-color: rgb(255, 255, 255); 
 

}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* Navigation */
navBar {
  grid-column-start: 3;
  grid-column-end: span 1;
  grid-row-start: 3;
  grid-row-end: span 1;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  background-color: rgb(211, 211, 211);  
  align-items: center;
}
.NavBtn {
  box-shadow:5px 5px 5px rgb(137, 137, 137) ; 
  border: #a9a9a9;  
  border-width: 2px;
  border-style:solid;
  border-radius: 10px;
  padding:none;
  margin:none;
  background-color:#ededeb ;  
}

/* Board Page */
kanbanBoard {
  grid-column-start: 3;
  grid-column-end: span 1;
  grid-row-start: 4;
  grid-row-end: span 7;
  background-color: #cedcbf;
  display: flex;
  justify-content: flex-start;
  flex-wrap: wrap;
  flex-direction: row;
  padding: 20px;
}

parchContainer {
  padding: 15px;
  max-width: 230px;
  max-height: 235px;
  /* position:absolute; */
  /* background-color: aqua; */
}
textarea.listTitle {  
  color:navy; 
  font-size: 30px;
  text-decoration: none;
  border: none;
  background-color: transparent;
  position: relative;
  bottom: 140px;
  right: -20px;
  width: 170px;
  height: fit-content;
  text-align: center;  
}

/* Footer */
footerBar {
  grid-column-start: 3;
  grid-column-end: span 1;
  grid-row-start: span 1;
  grid-row-end: 12;
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  background-color: rgb(211, 211, 211);  
  align-items: center;
  padding: 20px;
}
